import React from "react";
import Item from "./Item";
import "./techStack.css";

const TechStack = () => {
  return (
    <div
      className='container-fluid align-items-center justify-content-center d-flex'
      style={{ marginTop: "5rem" }}
    >
      <div className='row w-100 p-0 w-0'>
        <Item name='PHP' progress={100} pWeek={80} pMonth={65} />
        <Item name='Python' progress={90} pWeek={90} pMonth={70} />
        <Item name='Java' progress={80} pWeek={70} pMonth={80} />
        <Item name='C++' progress={70} pWeek={60} pMonth={90} />
        <Item name='Kotlin' progress={60} pWeek={85} pMonth={80} />
        <Item name='Groovy' progress={50} pWeek={75} pMonth={60} />
      </div>
    </div>
  );
};

export default TechStack;
